html,
body {
  font-family: $main;
  background-color: $main-bg;

  // font-size: clamp(16px, 0.4rem + 1.2vw, 20px);
  // line-height: clamp(24px, 0.8rem + 1.5vw, 28px);
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  color: $main-text;
  
  margin: 0 auto;
  width: 100%;
  height: 100%;

  scroll-behavior: smooth;
}
//   clamp(min, current, max)
//   window 1440px / max font-size = 44px
//   1vw = 14.4px
//   44px / 14.4px = 3.05vw

#root{
  width: 100%;
  height: 100%;
}

body.hidden{
  overflow: hidden;
}

address {
  font-style: normal;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

h1{
  font-size: clamp(28px, 1.1rem + 1.8vw, 44px);
  line-height: clamp(32px, 1.2rem + 1.8vw, 48px);
  color: $white-text;
  font-weight: 400;
}
h2{
  font-size: clamp(24px, 0.9rem + 1.8vw, 40px);
  line-height: clamp(32px, 1.1rem + 1.8vw, 48px);
  color: $white-text;
  font-weight: 400;
}
h3{
  font-size: clamp(22px, 0.7rem + 1.8vw, 32px);
  line-height: clamp(28px, 1.1rem + 1.8vw, 36px);
  color: $white-text;
  font-weight: 500;
}
h4{
  font-size: clamp(18px, 0.5rem + 2vw, 32px);
  line-height: clamp(24px, 0.8rem + 2vw, 36px);
  color: $white-text;
  font-weight: 500;
}

// link hover focus active 
.link-hfa{
  transition: $transition;

  &:hover,&:focus{
    color: $hover-accent-text;
    & svg{
      transition: $transition;
      color: $hover-accent-text;
    }
  }
  &:active{
    color: $active-accent-text;

    & svg{
      transition: $transition;
      color: $active-accent-text;
    }
  }
}


.bg-card{
  background-color: $card-bg;  
}

.br-16-to-24{
  border-radius: clamp(16px, 0.6rem + 1.1vw, 24px);
}

.br-12{
  border-radius: 12px;
}

.br-20{
  border-radius: 20px;
}

.br-24{
  border-radius: 24px;
}

.br-24-to-20{
  border-radius: clamp(20px, 1.7rem - 1.5vw, 24px);
}

// width height 
.w-32-to-48{
  width: clamp(32px, 1rem + 4vw, 48px);
}
.h-32-to-48{
  height: clamp(32px, 1rem + 4vw, 48px);
}



// padding margin
.p-lr-12-to-32{
  padding-left: clamp(12px, 0.125rem + 2.3vw, 32px);
  padding-right: clamp(12px, 0.125rem + 2.3vw, 32px);
}
.p-lr-12-to-56{
    padding-left: clamp(12px, -0.4rem + 5vw, 56px);
    padding-right: clamp(12px, -0.4rem + 5vw, 56px);
}

.p-tb-12-to-16{
  padding-top: clamp(12px, 0.625rem + 0.5vw, 16px);
  padding-bottom: clamp(12px, 0.625rem + 0.5vw, 16px);
}
.p-tb-16-to-32{
  padding-top: clamp(16px, 0.4rem + 1.9vw, 32px);
  padding-bottom: clamp(16px, 0.4rem + 1.9vw, 32px);
}
.p-tb-32-to-56{
  padding-top: clamp(32px, 0.4rem + 6.52vw, 56px);
  padding-bottom: clamp(32px, 0.4rem + 6.52vw, 56px);
}

.mb-12{
  margin-bottom: 12px;
}
.mb-16{
  margin-bottom: 16px;
}
.mb-24{
  margin-bottom: 24px;
}
.mb-32{
  margin-bottom: 32px;
}

.mb-24-to-32{
  margin-bottom: clamp(24px, 1rem + 1vw, 32px);;
}

.mb-16-to-24 {
  margin-bottom: clamp(16px, 0.75rem + 1vw, 24px);
}
.mb-40-to-32{
  margin-bottom: clamp(40px, 1.25rem + 1vw, 32px);
}
.mb-44-to-68{
  margin-bottom: clamp(44px, 2rem + 3vw, 68px);
}
.mb-88-to-120{
  margin-bottom: clamp(88px, 3.5rem + 5vw, 120px);
}
.mt-24-to-32{
  margin-top: clamp(24px, 1.25rem + 1vw, 32px);;
}
.ml-24{
  margin-left: 24px;
}


// flex
.flex {
  display: flex;
}
.fd-row{
  flex-direction: row;
}
.fd-col{
  flex-direction: column;
}
.wrap{
  flex-wrap: wrap;
}
.jc-sb{
  justify-content: space-between;
}
.ai-c{
  align-items: center;
}
.gap-12-to-32{
  gap: clamp(12px, 0.125rem + 2.5vw, 32px)
}
.gap-16-to-24{
  gap: clamp(16px, 0.394rem + 2.5vw, 24px);
}
.gap-24-to-32{
  gap: clamp(24px, 1rem + 1vw, 32px);
}
.gap-44-to-68{
  gap: clamp(44px, 2rem + 4vw, 68px)
}
.gap-88-to-120{
  gap: clamp(88px, 3.5rem + 5vw, 120px);
}

// error
.error-message{
  // position: absolute;
  // bottom: -28px;
  // left: 24px;
  // margin-top: 12px;
  margin-left: 24px;

  font-size: 12px;
  line-height: 16px;
  color: $error-text;
  opacity: 0;

  height: 100%;
  max-height: 0px;
  overflow: hidden;

  transition: $transition;

  &.list{
    list-style: disc;
    margin-left: 0;
    padding-left: 36px;
  }
  &.list.restore{
    opacity: 1;
    margin-top: 12px;
    max-height: 50px;
    color: $forgot-text;
  }
  &.list.restore.error{
    color: $error-text;
  }
}
.error-message.error{
  opacity: 1;
  margin-top: 12px;
  max-height: 50px;
}

 /////////////// LOADER   
  .loader-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;

    transition: $transition;

    &.loader-overlay-show{
        opacity: 1;
    }

    &.bread{
      position: relative;
      background-color: transparent;
      width: 20px;
      height: 20px;
    }
}







